export default "/*#region Mid Geral*/\n.meio {\n  width: 90%;\n  margin-top: calc((100vw / 5) + 20px);\n  margin-left: 5%;\n}\n.meio .top {\n  background-color: var(--dark-space-cadet);\n  width: 100%;\n  min-height: 40px;\n  box-sizing: border-box;\n  padding: 0px 10px 0px 10px;\n  border-radius: 10px 10px 0px 0px;\n}\n.meio .top h3 {\n  color: var(--white) !important;\n}\n.meio .content, .meio .bot {\n  background-color: var(--lavander);\n  overflow: hidden;\n  border-radius: 10px;\n}\n/*#endregion*/\n/*#region Medicamentos*/\n.meio .content {\n  width: 100%;\n  padding-bottom: 20px;\n  margin-bottom: 180px;\n}\n.meio .content .line {\n  width: 90%;\n  margin-left: 5%;\n  border-color: var(--state-blue);\n  border-width: 2px 0px 0px 0px;\n  border-style: solid;\n}\n.medicamento {\n  width: 90%;\n  min-height: 200px;\n  margin-left: 5%;\n  padding: 15px;\n  box-sizing: border-box;\n}\n.medicamento img {\n  width: 35%;\n  max-height: 170px;\n}\n.medicamento .text {\n  width: 60%;\n  margin-left: 5%;\n}\n.medicamento .text h3 {\n  margin-top: 7px;\n  font-size: 2em;\n}\n.medicamento .text p {\n  margin-top: 7px;\n  font-size: 1.3em;\n  color: var(--space-cadet) !important;\n}\n/*#endregion*/\n/*#region Alarme*/\n.alarm {\n  margin-bottom: 180px;\n}\n.alarm .medicamento {\n  background-color: var(--lavander);\n  width: 100%;\n  margin: 0;\n  border-radius: 0px 0px 10px 10px;\n}\n.alarm div:nth-of-type(3) {\n  justify-content: space-between;\n}\n.alarm button {\n  width: calc((90vw / 2) - 20px);\n  height: calc((90vw / 2) - 20px);\n  margin-top: 30px;\n  border-radius: 20px;\n  padding: 10px;\n  box-sizing: border-box;\n  font-size: 2em;\n  position: initial !important;\n}\n.alarm button svg {\n  width: 100%;\n  height: 55%;\n}\n.alarm .danger {\n  fill: var(--white);\n}\n/*#endregion*/\n/*#region Bottom*/\n.meio .bot {\n  width: 90%;\n  position: fixed;\n  bottom: 20px;\n}\n.meio .bot .mid {\n  height: 100px;\n}\n.meio .bot .mid button {\n  width: 90%;\n}\n/*#endregion*/\n.naoTem {\n  height: 50vh;\n}\n.naoTem svg {\n  width: 40%;\n  fill: var(--lavander);\n  margin-top: 10vh;\n}\n/*# sourceMappingURL=data:application/json;base64, */";